.card {
  font-size: 1.3rem;
  display: flex;
  flex-direction: column;
  margin-top: 5px;
  padding: 15px;
  width: 210px;
  height: 100%;
  box-shadow: 10px 10px 23px rgb(38, 57, 77), -10px -10px 23px rgb(38, 57, 77);
  /* box-shadow: rgb(38, 57, 77) 0px 25px 35px -5px; */
  border: 1px solid black;
  border-radius: 10px;
  background-image: linear-gradient(
    to right,
    #54b3d6,
    #54b3d6 50%,
    #f4d06f 50%
  );
  background-size: 200% 100%;
  background-position: -100%;
  transition: all 1s ease-in-out;
}
.card:before {
  background: #54b3d6;
  display: block;
  /* position: absolute; */
  bottom: -3px;
  left: 0;
  width: 0;
  height: 3px;
}
.card:hover {
  background-position: 0;
  transform: scale(1.3);
}
